/*------------------------------------*\
    #BUTTON GROUP
\*------------------------------------*/

/**
 * 1) A button group displays buttons side-by-side.
 */
.c-btn-group {
  display: flex;
}

/**
 * 1) A button group displays buttons side-by-side.
 */
.c-btn-group--responsive {
  flex-direction: column;

  @media all and (min-width: @bp-small) {
    flex-direction: row;
  }
}